/** @format */

section.PlanSummary {
  background-color: #f3f7f9;
  width: 100%;
  font-family: Roboto;
}

section.PlanSummary .Parent {
  width: 90%;
  margin: auto;
}

.Billing_Detail {
  margin-top: 25px;
}

.Billing_Detail div {
  border-radius: 8px;
  margin-top: 3.5%;
  padding: 15px;
  background-color: #f6f9fb;
}

.Billing_Detail div:nth-child(1) span {
  display: grid;
  grid-template-columns: 70% 30%;
}

.Billing_Detail div:nth-child(2) span {
  display: grid;
  grid-template-columns: 70% 30%;
}

.Billing_Detail div:nth-child(2) span h6 {
  align-self: flex-end;
  text-align: right;
}

.Billing_Detail h1 {
  font-family: Roboto;
  font-size: 15px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #7b8aa2;
  text-align: left;
}

.Billing_Detail p {
  font-family: Roboto;
  font-size: 13px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #7b8aa2;
  text-align: left;
  margin-top: 10px;
}

.Billing_Detail h6 {
  color: #7b8aa2;
  text-decoration: underline;
}

.Summary_Title {
  margin-top: 25px;
}

.Summary_Title h1 {
  margin: 10px;
  font-family: Roboto;
  font-size: 18px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #24272c;
  text-align: left;
}

.Detail {
  margin-top: 20px;
}

section.PlanSummary input {
  height: 40px !important;
}

.Detail .Sub {
  margin: 10px;
  display: grid;
  grid-template-columns: 70% 30%;
}

.Detail div h3 {
  font-family: Roboto;
  font-size: 15px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #566171;
  align-self: center;
  text-align: left;
}

.Detail div h3:nth-of-type(2n) {
  text-align: right;
}

.section.PlanSummary input {
  border-radius: 4px;
  height: 40px !important;
}

input:focus {
  font-size: 13px;
}

.Plan_Dropdown {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 0px !important;
  transform: translateY(-100%);
  overflow-y: hidden;
  opacity: 0;
  display: none;
  transition: 0.5s ease-in-out;
}
.Plan_Dropdown > div {
  margin-top: 0px !important;
  padding: 0px !important;
}

.Plan_Dropdown h1 {
  margin-bottom: 5px;
  text-transform: capitalize;
}

.Plan_Dropdown p {
  margin-top: 3px !important;
  margin-bottom: 6px;
}

.Toggle_DropDown {
  display: grid;
  transform: translateY(0);
  overflow-y: hidden;
  opacity: 1;
}

.Total_Amount {
  position: relative;
  margin: 30px 10px 10px 10px;
  display: grid;
  grid-template-columns: 60% 40%;
}

.Total_Amount:before {
  content: "";
  position: absolute;
  top: -10px;
  left: 0;
  border-radius: 8px;
  background-color: #f2f3f7;
  width: 100%;
  height: 2px;
}

.Total_Amount h1 {
  font-family: Roboto;
  font-size: 15px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.6;
  letter-spacing: normal;
  text-align: left;
  color: #566171;
}

.Total_Amount span h1 {
  text-align: right;
}

.Total_Amount span h6 {
  opacity: 0.7;
  font-family: Roboto;
  font-size: 10px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: right;
  color: #7b8aa2;
}

section.PlanSummary button {
  margin: 10px;
  display: flex;
}

section.PlanSummary button svg {
  margin-left: 10px;
}
